	module.exports = {"data":{"markdownRemark":{"html":"<p>String values in JavaScript may be written with single or double quotes, so long as you start and end with the same type of quote. Unlike some languages, single and double quotes are functionally identical in JavaScript.</p>\n<p><code>\"This string has \\\"double quotes\\\" in it\"</code></p>\n<p>The value in using one or the other has to do with the need to escape quotes of the same type. If you have a string with many double quotes, this can be difficult to read and write. Instead, use single quotes:</p>\n<p><code>'This string has \"double quotes\" in it. And \"probably\" lots of them.'</code></p>","fields":{"slug":"/certificates/quoting-strings-with-single-quotes/"},"frontmatter":{"title":"Quoting Strings with Single Quotes"}}},"pathContext":{"slug":"/certificates/quoting-strings-with-single-quotes/"}}

